<template>
<div>
  <van-field
    v-model="username"
    name=""
    label=""
    placeholder=""
    :rules="[{ required: true, message: '' }]"
  />
  <van-field
    v-model="password"
    type="password"
    name=""
    label=""
    placeholder=""
    :rules="[{ required: true, message: '' }]"
  />
  <div style="margin-top:2rem">
    <van-button round block type="info" @click="login" >
      
    </van-button>
    <van-button round block type="info" @click="register" style="margin-top:1rem">
      
    </van-button>
  </div>
</div>

</template>
<script>
export default {
    data() {
      return {
        username: '',
        password: '',
      }
    },
    methods: {
      login() {
        const reg = /^[a-z0-9]{3,12}$/i
        const u = this.username;
        const p = this.password;
        if(!reg.test(u)){
          this.$dialog.alert({
            message: ''
          })
          return;
        }
        if(!reg.test(p)){
            this.$dialog.alert({
              message:""
            });
            return;//
        } 
        this.axios.get("/Login",{
          params:{
            uname:u,
            upwd:p
          }
        })
        .then(res=>{
          if(res.data.code==-1){
            this.$dialog.alert({
              message:''
            })
          }else{
            this.$toast("");
            this.$router.back()
          }
        }) 
      },
      register(){
        const reg = /^[a-z0-9]{3,12}$/i
        const u = this.username;
        const p = this.password;
        if(!reg.test(u)){
          this.$dialog.alert({
            message: ''
          })
          return;
        }
        if(!reg.test(p)){
            this.$dialog.alert({
              message:""
            });
            return;//
        } 
        this.axios.get("/Register",{
          params:{
            uname:u,
            upwd:p
          }
        })
        .then(res=>{
          if(res.data.code==1){
            this.$dialog.alert({
              message:''
            })
          }else{
            this.$toast("");
          }
        }) 
      }
    },
  }
</script>
